@font-face {
  font-family: "Montserrat";
  src: local("montserrat-regular"), url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
.page {
  height: 100%;
  scroll-behavior: smooth;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}
@media (max-width: 1023px) {
  .container {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 12px;
  }
}

.header {
  flex: 0 0 auto;
  min-height: 60px;
  background-color: #a1b983;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
  background-color: #94aead;
}

.cv-block__container {
  margin: 30px 0;
  display: flex;
}
@media (max-width: 767px) {
  .cv-block__container {
    flex-direction: column;
  }
  .cv-block__container picture {
    margin: 0 auto;
  }
}

.cv-block__about {
  margin-left: 20px;
}
@media (max-width: 767px) {
  .cv-block__about {
    margin: 0;
    margin-top: 10px;
    text-align: center;
  }
}
.cv-block__about h3,
.cv-block__about p {
  margin: 0;
}
.cv-block__about h3 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 24px;
  line-height: 26px;
  font-weight: 600;
  font-style: normal;
  padding-bottom: 10px;
}
@media (max-width: 1023px) {
  .cv-block__about h3 {
    font-size: 18px;
    line-height: 22px;
  }
}
.cv-block__about p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 400;
  font-style: normal;
}
@media (max-width: 1023px) {
  .cv-block__about p {
    font-size: 14px;
    line-height: 16px;
  }
}

.cv-block__features-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .cv-block__features-list {
    justify-content: center;
  }
}

.cv-block__features-item:not(:last-child) {
  margin-right: 5px;
}
@media (max-width: 767px) {
  .cv-block__features-item svg {
    width: 30px;
    height: 30px;
  }
}

.cv-block__about-achievements {
  display: flex;
}
@media (max-width: 767px) {
  .cv-block__about-achievements {
    justify-content: center;
  }
}
.cv-block__about-achievements a:not(:last-child) {
  margin-right: 10px;
}

.cv-block__about p {
  padding-bottom: 10px;
}

.cv-block__about-hh a {
  display: block;
  margin-top: 10px;
}

@media (max-width: 1023px) {
  .cv-block__about-hh-icon {
    width: 30px;
    height: 30px;
  }
}
.cv-block__about-hh-icon:hover, .cv-block__about-hh-icon:focus-visible {
  transition: 0.3s ease-in-out;
  transform: scale(1.1);
}
.cv-block__about-hh-icon:active {
  transform: scale(0.9);
}

.project h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 32px;
  line-height: 36px;
  font-weight: 700;
  font-style: normal;
  text-align: center;
  margin: 0;
  padding-bottom: 20px;
}

.project__flex {
  display: flex;
  justify-content: space-around;
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.project__box {
  display: flex;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
}
.project__box .project__hidden-box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column wrap;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0;
  border-radius: 60px;
  padding: 0 40px;
}
.project__box .project__hidden-box h3,
.project__box .project__hidden-box p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  text-align: center;
  margin: 0;
  padding-bottom: 15px;
}
.project__box .project__hidden-box p {
  font-size: 10px;
  line-height: 14px;
}
.project__box .project__hidden-box a {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 220px;
  min-height: 55px;
  border: 2px solid #a1b983;
  border-radius: 30px;
  color: #ffffff;
}
.project__box .project__hidden-box a:hover, .project__box .project__hidden-box a:focus-within {
  transition: 0.3s ease-in-out;
  background-color: #a1b983;
}
.project__box .project__hidden-box a:active {
  opacity: 0.7;
}
.project__box:hover .project__hidden-box, .project__box:focus-within .project__hidden-box, .project__box:active .project__hidden-box {
  transition: 0.3s ease-in-out;
  z-index: 2;
  opacity: 1;
}

.project__img img {
  border-radius: 60px;
}

.footer {
  flex: 0 0 auto;
  min-height: 60px;
  background-color: #a1b983;
  padding-top: 6px;
}

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  width: 200px;
  margin: 0 auto;
}

.overlay {
  position: fixed;
  animation: bounce 0.6s;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: 34;
}
.overlay--hidden {
  display: none;
}